:root {
    --default-padding: 20px;
    --padding-small: 8px;
    --padding-medium: 10px;
    --default-margin: 10px;
    --margin-small: 5px;
    --button-padding: 10px 20px;
    --footer-padding: 1rem 0;
    --sidebar-width: 30vw; 
}

@media (max-width: 768px) {
    :root {
        --default-padding: 15px;
        --default-margin: 8px;
        --sidebar-width: 100%;
    }
}
